'use client';

import React, { useState } from 'react';
import { Menu, X, Phone } from './Icons';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const pathname = usePathname();

  const handleMobileLinkClick = () => {
    setIsOpen(false);
  };

  const navLinks = [
    { name: 'Home', href: '/' },
    { name: 'Services', href: '/#solutions' },
    { name: 'ROI Calculator', href: '/#calculator' },
    { name: 'Pricing', href: '/#pricing' },
  ];

  return (
    <nav className="bg-primary-900 shadow-sm sticky top-0 z-40 border-b border-primary-800">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex">
            <Link href="/" className="flex-shrink-0 flex items-center">
              <div className="h-8 w-8 border-2 border-accent-500 rounded-full flex items-center justify-center mr-2">
                 <span className="text-accent-500 font-bold text-[10px]">P-AI</span>
              </div>
              <span className="font-bold text-xl text-white tracking-widest">PISTLE AI</span>
            </Link>
            <div className="hidden md:ml-6 md:flex md:space-x-8">
              {navLinks.map((link) => (
                <Link 
                  key={link.name}
                  href={link.href}
                  className={`${
                    pathname === link.href 
                      ? 'border-accent-500 text-white' 
                      : 'border-transparent text-slate-300 hover:border-slate-300 hover:text-white'
                  } inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium transition-colors`}
                >
                  {link.name}
                </Link>
              ))}
            </div>
          </div>
          <div className="hidden md:flex items-center">
            <Link
              href="/contact"
              className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-primary-900 bg-accent-500 hover:bg-accent-400 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-accent-500 transition-colors"
            >
              <Phone className="h-4 w-4 mr-2" />
              Contact Us
            </Link>
          </div>
          <div className="-mr-2 flex items-center md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="inline-flex items-center justify-center p-2 rounded-md text-slate-400 hover:text-white hover:bg-primary-800 focus:outline-none"
            >
              {isOpen ? <X className="block h-6 w-6" /> : <Menu className="block h-6 w-6" />}
            </button>
          </div>
        </div>
      </div>

      {isOpen && (
        <div className="md:hidden bg-primary-800 shadow-xl">
          <div className="pt-2 pb-3 space-y-1">
            {navLinks.map((link) => (
              <Link
                key={link.name}
                href={link.href}
                onClick={handleMobileLinkClick}
                className={`${
                  pathname === link.href
                    ? 'bg-primary-900 border-accent-500 text-white'
                    : 'border-transparent text-slate-300 hover:bg-primary-700 hover:border-slate-300 hover:text-white'
                } block pl-3 pr-4 py-2 border-l-4 text-base font-medium transition-colors`}
              >
                {link.name}
              </Link>
            ))}
            <Link 
              href="/contact"
              onClick={handleMobileLinkClick}
              className="w-full text-left border-transparent text-accent-500 font-bold hover:bg-primary-700 block pl-3 pr-4 py-2 border-l-4 text-base transition-colors"
            >
              Contact Us
            </Link>
          </div>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
